app.controller('BitbucketController', ['$scope', '$q', '$window', 'loggedInUser',
    'flightRecorder', 'pivottableService', 'configurationService', 'viewService',
    function ($scope, $q, $window, loggedInUser, flightRecorder, pivottableService, configurationService, viewService) {

        var AP = window.RA || window.AP;

        init();

        function init() {
            AP.resize();

            $scope.replyTo = loggedInUser.displayName + ' <' + loggedInUser.emailAddress + '>';

            var closeDialogFunction = function() {
                $scope.$apply(function() {
                    $scope.loading = true;
                });
                flightRecorder.sendEmail($scope.includeSupportData.enabled, $scope.replyTo, $scope.title, $scope.content, $scope.includeSupportData.screenshot, $scope.includeSupportData.error).then(function (created) {
                    AP.Dialog.close('submit');
                }).catch(function (response /* or error */) {
                    AP.flag.create({
                        title: 'Unexpected error',
                        body: response.status ? response.status + ' ' + response.data : /* error */ response,
                        type: 'error'
                    });
                });
            };

            var paramsDeferred = $q.defer(), $fields, $restricted, $filters, $projects, $jiraConfig, $isAdmin, $statuses, $user;
            AP.dialog.getCustomData(function(params) {
                $scope.title = params.errorMessage;
                if (params.stackTrace) {
                    var content = ['', '', '---- Error Stack Trace ----'];
                    content.push.apply(content, params.stackTrace);
                    $scope.content = content.join('\n');
                }
                $scope.includeSupportData = {enabled: false, screenshot: params.screenshot, error: params.error};
                if (params.options) {
                    $scope.includeSupportData.options = params.options;
                    if (!flightRecorder.enabled) {
                        flightRecorder.setEnabled(true);
                    }
                    flightRecorder.timesheetParams = params.timesheetParams; // redundant?
                    if (Object.keys(params.timesheetParams).length == 0) {
                        if (angular.isFunction(AP.cookie.read)) {
                            var COOKIE_NAME_LOCATION_SEARCH = 'timereports-location-search'; // FIXME: + COOKIE_NAME_SUFFIX
                            AP.cookie.read(COOKIE_NAME_LOCATION_SEARCH, function (data) {
                                flightRecorder.cookie(data);
                                var routeParams = TimesheetUtils.getJson(data);
                                if (routeParams && routeParams.viewId) {
                                    viewService.getViews(routeParams.viewId, loggedInUser).then(paramsDeferred.resolve);
                                } else {
                                    paramsDeferred.resolve(routeParams);
                                }
                            });
                        } else {
                            paramsDeferred.resolve({});
                        }
                    } else {
                        if (params.timesheetParams.viewId) {
                            viewService.getViews(params.timesheetParams.viewId, loggedInUser).then(paramsDeferred.resolve);
                        } else {
                            paramsDeferred.resolve(params.timesheetParams);
                        }
                    }
                    $fields = pivottableService.getFields();
                    $restricted = pivottableService.restrictedByAuditorsGroups(loggedInUser);
                    $filters = pivottableService.getFilters();
                    $projects = configurationService.getProjects();
                    $jiraConfig = pivottableService.getJiraConfiguration();
                    $isAdmin = pivottableService.isAdministrator();
                    $statuses = configurationService.getAllStatuses();
                    if (params.options.user && loggedInUser.key != params.options.user) {
                        $user = pivottableService.getUserInfo(params.options.user);
                    }
                }
                $scope.$apply();
            });

            AP.events.on('dialog.submit', closeDialogFunction);
            AP.dialog.disableCloseOnSubmit();

            $scope.$watch("includeSupportData.enabled", function (value) {
                if (value) {
                    var submitButton = AP.dialog.getButton('submit');
                    submitButton.disable();
                    $q.all([paramsDeferred.promise, $fields, $restricted, $filters, $projects, $jiraConfig, $isAdmin]).then(function (values) {
                        var routeParams = values[0], restricted = values[2];
                        if (!$scope.includeSupportData.error) {
                            $scope.includeSupportData.error = {};
                        } else if (!angular.isObject($scope.includeSupportData.error)) {
                            $scope.includeSupportData.error = {error: $scope.includeSupportData.error};
                        }
                        $scope.includeSupportData.error.options = $scope.includeSupportData.options;
                        if ($scope.includeSupportData.options.restrictable && restricted) {
                            $scope.includeSupportData.error.restricted = true;
                        } else {
                            var $pivotTable = pivottableService.getPivotTable(loggedInUser, $scope.includeSupportData.options);
                        }
                        $q.when($pivotTable).then(function() {
                            if ($scope.SubmitIssueForm.$valid) {
                                submitButton.enable();
                            }
                        });
                    });
                }
            });

        }
    }
]);
